@use 'variables' as *;
@import 'mixins';

#mobile-wrapper {
  display: flex;
  width: 100%;
  justify-content: space-between;
}

.nav-wrapper {
  width: 100%;
  padding: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: absolute;
  justify-content: space-between;
  font-family: $font-mono;

  @include breakpoint(sm) {
    flex-direction: row;
  }

  .mobile-menu-icon {
    width: 46px;
    fill: $font-color-primary;
    opacity: 0.75;
  }
}

nav {
  top: 0;
  right: 0;
  left: 0;
  padding: 1rem;
  display: flex;
  position: absolute;
  background-color: #0000006b;
  z-index: 10;
  box-shadow: 0 0 10px #00000052;
  //backdrop-filter: blur(5px) opacity(0);
  transform: translate(0, -100%);
  //transition: transform 0.5s, backdrop-filter 0.5s;
  transition: all 0.5s;

  @include breakpoint(sm) {
    display: flex;
    flex-direction: column;
  }
  font-size: 1.2rem;
  align-items: center;

  ul {
    display: flex;
    flex-direction: column;
    gap: 0.2rem;
    margin: 0;
    list-style: none;

    @include breakpoint(sm) {
      padding: 0.5rem;
    }
  }

  li {
    color: $font-color-primary;
    display: inline-block;
    // font-size: 1.5rem;
  }

  a {
    color: $font-color-primary;
    padding: 6px 12px;
    text-decoration: none;
  }
}

.show-mobile-menu {
  transform: translate(0, 0);
  //backdrop-filter: blur(4px) opacity(1);
}
